import React from 'react';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class TitleHolderIndividual extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='Individual Title Holder' />
        <div className='form_inputs'>
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTU_383_Social_Security_Number' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTU_713_Drivers_License_Number' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTU_970_Date_Of_Birth' />
          <InputerForm {...this.props} FieldName='Finance_FTU_579_Marital_Status' />

        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTU_579-565_Lived_In_California_Last_5_Years' />
          <InputerForm {...this.props} FieldName='Finance_FTU_579_T_Address' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTU_261_Has_Declared_Bankrupt' />
          <InputerForm {...this.props} FieldName='Finance_FTU_261_T_Text_Explanation' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTU_533_Has_Unsatisfied_Judgements' />
          <InputerForm {...this.props} FieldName='Finance_FTU_533_T_Text_Explanation' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTU_991_881_Certified' />

        </div>
      </>
    );
  }
}
