import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class KitchenFeatures extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Kitchen Features' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFK_x01_Kitchen_Features' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFK_460_Has_Defects' />
          <InputerForm {...this.props} FieldName='SFK_460_Defects_Explanation' />
        </div>
      </>
    );
  }
}
