import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class ExteriorGrounds extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Exterior & Grounds' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFX_144_Exterior_Features' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFX_625_Has_Fences_Defects' />
          <InputerForm {...this.props} FieldName='SFX_625_Fences_Defects_Explanation' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFX_521_Has_Retaining_Walls_Defects' />
          <InputerForm {...this.props} FieldName='SFX_521_Retaining_Walls_Defects_Explanation' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFX_209_Has_Water_Features_Defects' />
          <InputerForm {...this.props} FieldName='SFX_209_Water_Features_Defects_Explanation' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFX_947_Sprinkler_Type' />
          <InputerForm {...this.props} FieldName='SFX_338_Has_Area_Not_Covered_By_Sprinkler' />
          <InputerForm {...this.props} FieldName='SFX_338_Area_Not_Covered_By_Sprinkler_Description' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFX_930_Has_Outdoor_Defects' />
          <InputerForm {...this.props} FieldName='SFX_930_Outdoor_Defects_Explanation' />
        </div>
      </>
    );
  }
}
