import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class SafetySecurity extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Safety & Security' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFS_604_Additional_Safty_Features' />
          <InputerForm {...this.props} FieldName='SFS_756_Has_Quick_Release_Mechanism' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFS_847_Has_Defects' />
          <InputerForm {...this.props} FieldName='SFS_847_Defects_Explanation' />
        </div>
      </>
    );
  }
}
