import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class OtherFeatures extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Other' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFO_611_Additional_Features' />
          <InputerForm {...this.props} FieldName='SFO_223_Exhaust_Fan_Location' />
          <InputerForm {...this.props} FieldName='SFO_223_Other_Explanation' />
          <InputerForm {...this.props} FieldName='SFO_776_Assigned_Storage_Details' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFO_196_Has_Defects' />
          <InputerForm {...this.props} FieldName='SFO_196_Defects_Explanation' />
        </div>
      </>
    );
  }
}
