import React from 'react';
import { ContactUsForm } from '../../components/ContactUs/ContactUsForm';

export default function GlobalThankYou (props) {
  return (
    <>
      <div className='steppers'>
        <div className='step_title'>Thank you for completing this section!<br /> You're one step closer to selling your home.</div>
        <div className='step_texts'>
          <div>If you have any questions, do not hesitate to <ContactUsForm handleSubmit={props.SubmitContactUsHandler}><span className='blue_text'>reach out </span></ContactUsForm>to us at <a href='mailto:hello@nilehome.com' className='blue_text'>hello@nilehome.com</a> or to your represented NILE Advisor.</div>
        </div>
      </div>
    </>
  );
}
