import React from 'react';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class TitleHolderLLC extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='LLC Title Holder' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTC_171_T_Llc_Tax_Id' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTC_171-u_Upload_Llc_Documents' />
        </div>
      </>
    );
  }
}
