import React from 'react';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class TitleHolderCorporate extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='Corporate Title Holder' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTO_355_Corporation_Tax_Id_Number' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTO_355-u_Upload_Corporate_Documents' />
        </div>
      </>
    );
  }
}
