import React from 'react';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class PolyPipes extends React.Component {
  render () {
    if (this.props.InputValues.BYP_502_Active === undefined || this.props.InputValues.BYP_502_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='PolyPipes (PRE 1995)' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYP_983_PolyPipes' />
          <InputerForm {...this.props} FieldName='BYP_983_PolyPipes_Explanation' />
        </div>
      </>
    );
  }
}
