import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class HeatingCooling extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Heating & Cooling' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFH_079_Heating_Method' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFH_805_Cooling_Method' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFH_977_Has_Fireplace' />
          <InputerForm {...this.props} FieldName='SFH_977_Fireplace_Number' />
          <InputerForm {...this.props} FieldName='SFH_977_Fireplace_Items' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFH_218_Has_Heating_Cooling_Defects' />
          <InputerForm {...this.props} FieldName='SFH_218_Defects_Text_Explanation' />
        </div>
      </>
    );
  }
}
