export default class ReportsInspectionsDisclosures extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Reports, Inspections, Disclosures' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='primary_questionnaire_pqr_653_reports_etc' />
          <InputerForm {...this.props} FieldName='primary_questionnaire_pqr_653_reports_etc_explanation' />
          <InputerForm {...this.props} FieldName='primary_questionnaire_pqr_653_reports_etc_files' />
        </div>
      </>
    );
  }
}
