import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class PlumbingElectrical extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Plumbing & Electrical Features' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFL_083_Plumbing_Features' />
          <InputerForm {...this.props} FieldName='SFL_547_Water_Heater_Power_Type' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFL_932_Laundry_Equipments' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFL_479_Has_220_Volt_Wiring' />
          <InputerForm {...this.props} FieldName='SFL_479_Electrical_Features' />
          <InputerForm {...this.props} FieldName='SFL_147_Other_Explanation' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFL_874_Has_Pluming_Defects' />
          <InputerForm {...this.props} FieldName='SFL_874_Pluming_Defects_Explanation' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFL_2888_Has_Electrical_Defects' />
          <InputerForm {...this.props} FieldName='SFL_2888_Electrical_Defects_Explanation' />
        </div>
      </>
    );
  }
}
