import React from 'react';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class Asbestos extends React.Component {
  render () {
    if (this.props.InputValues.BYA_581_Active === undefined || this.props.InputValues.BYA_581_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='Asbestos (PRE 1978)' />

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYA_239_Asbestos' />
          <InputerForm {...this.props} FieldName='BYA_239_Asbestos_Explanation' />
        </div>
      </>
    );
  }
}
