import React from 'react';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class Tax extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='TAX' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTX_x01-n' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTX_x01_Confirm_Paid_Tax' />
          <InputerForm {...this.props} FieldName='Finance_FTX_x01_T_Explanation' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTX_439_Is_Foreign_Corp' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTX_996_Is_Non_Resident_Alien' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTX_731_Is_Sold_Under_301_Exchange_Program' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTX_804_Is_Investement_Property' />
        </div>
      </>
    );
  }
}
