import React from 'react';
import imageArrowW from '../../../images/arrow_w.svg';

export default class FooterMiddleStepReadOnly extends React.Component {
  render () {
    return (
      <>
        <div className='prev'>
          <div className='prev_btn animated_fastest' onClick={() => this.props.SwitchTabHandler(this.props.SelectedTab - 1)}>Previous</div>
        </div>
        <div className='nexts'>
          <div className='save_btn animated_fastest' onClick={async () => this.props.GotoDashboardHandler()}>Exit</div>
          <div className='next_btn animated_fastest' onClick={() => this.props.SwitchTabHandler(this.props.SelectedTab + 1)}>Next
            <div className='lm_arrow'><img src={imageArrowW} width='100%' alt='Arrow W' /></div>
          </div>
        </div>
        <div className='clearer' />
      </>
    );
  }
}
