import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class FinalQuestions extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Final Questions' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFF_493_Other_Defects' />
          <InputerForm {...this.props} FieldName='SFF_493_Other_Defects_Explanation' />
        </div>
      </>
    );
  }
}
