import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class GeneralStructureFeatures extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='General Structure Features' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFG_062_Foundation_Type' />
          <InputerForm {...this.props} FieldName='SFG_062_Has_Slab_Defects_Title' />
          <InputerForm {...this.props} FieldName='SFG_062_Has_Foundation_Defects_Title' />
          <InputerForm {...this.props} FieldName='SFG_062_Has_Slab_Foundation_Defects' />
          <InputerForm {...this.props} FieldName='SFG_062_370_Slab_Defects_Explanation_Title' />
          <InputerForm {...this.props} FieldName='SFG_062_370_Foundation_Defects_Explanation_Title' />
          <InputerForm {...this.props} FieldName='SFG_062_370_Slab_Foundation_Defects_Explanation' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFG_454_Structure_Features' />
          <InputerForm {...this.props} FieldName='SFG_454_Structure_Features_Other_Explanation' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFG_070_Roof_Type' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFG_461_Roof_Age' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFG_1272_Has_Roof_Defects' />
          <InputerForm {...this.props} FieldName='SFG_1272_Has_Roof_Defects_Explanation' />
          <InputerForm {...this.props} FieldName='SFG_070_Roof_Type_Explanation' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFG_6237_Defects' />
          <InputerForm {...this.props} FieldName='SFG_6237_Defects_Explanation' />
        </div>
        <div />
      </>);
  }
}
