import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class LeasedLienedItems extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Leased or Liened Items' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFD_998_Has_Leased_Liened_Items' />
          <InputerForm {...this.props} FieldName='SFD_762_Leased_Liened_Items' />
          <InputerForm {...this.props} FieldName='SFD_762_Other_Items_Explanation' />
        </div>
      </>
    );
  }
}
