import React from 'react';

export default class Header extends React.Component {
  render () {
    const {
      PropertyData
    } = this.props;

    var PropertyYearBuilt = 9999;
    var PropertyYearBuiltDisplay = 'UNKNOWN';
    if (PropertyData !== undefined && PropertyData.PropertyYearBuilt !== undefined) {
      PropertyYearBuilt = parseInt(PropertyData.PropertyYearBuilt);
      PropertyYearBuiltDisplay = parseInt(PropertyData.PropertyYearBuilt);
    }

    return (
      <>
        {
          PropertyData !== undefined &&
            <>
              <div className='step_texts' style={{ maxWidth: '100%', marginBottom: '40px' }}>
                <div>Property Built Year: {PropertyYearBuiltDisplay}</div>
                {
                  (PropertyYearBuilt >= 1996 && PropertyYearBuilt !== 9999) &&
                    <div style={{ marginTop: '15px' }}>No information required for properties built after 1996.</div>
                }
                {
                  (PropertyYearBuilt < 1996 && PropertyYearBuilt !== 9999) &&
                    <div style={{ marginTop: '15px' }}>Please fill in the following information.</div>
                }
              </div>
            </>
        }
      </>
    );
  }
}
