import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class OperatingCosts extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Operating Costs' />
        <InputerForm {...this.props} FieldName='SFC_582' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFC_999_Gas_Costs' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFC_060_Electric_Costs' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFC_042_Water_Costs' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFC_637_Other_Maintenance_Costs' />
        </div>
      </>
    );
  }
}
