import React from 'react';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class Loan extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const halfsProps = {
      ...this.props,
      WidthClassName: 'halfs'
    };

    const thirdsProps = {
      ...this.props,
      WidthClassName: 'thirds'
    };

    return (
      <>
        <FormSectionTitle Title='Loan' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTL_463_Has_Loan' />
        </div>

        {
          InputValuesHelper.GetInputValue(InputValues, 'Finance_FTL_463_Has_Loan') === Consts.OPTION_YES_VALUE &&
            <div className='form_inputs'>
              <div className='form_title2'>Loan 1</div>
            </div>
        }
        <>
          <div className='form_inputs'>
            <InputerForm {...halfsProps} FieldName='Finance_FTL_802-357_Institution_Name' />
            <InputerForm {...thirdsProps} FieldName='Finance_FTL_802-996_Account_Number' />
            <InputerForm {...thirdsProps} FieldName='Finance_FTL_802-731_Estimated_Payoff' />
          </div>
          <div className='form_inputs'>
            <InputerForm {...halfsProps} FieldName='Finance_FTL_802-383_Street_Address' />
            <InputerForm {...thirdsProps} FieldName='Finance_FTL_802-713_City' />
            <InputerForm {...thirdsProps} FieldName='Finance_FTL_802-970_State' />
            <InputerForm {...thirdsProps} FieldName='Finance_FTL_802-579_Postal_Code' />
            <div>
              <InputerForm {...thirdsProps} FieldName='Finance_FTL_802-919_Phone' />
              <InputerForm {...thirdsProps} FieldName='Finance_FTL_802-357_Fax' />
            </div>
          </div>
          <InputerForm {...this.props} FieldName='Finance_FTL_802-u_Statement' />
          <div className='form_inputs'>
            <InputerForm {...this.props} FieldName='Finance_FTL_630-501_Label' />
            <InputerForm {...thirdsProps} FieldName='Finance_FTL_630-501_Borrower_Ssn' />
            <InputerForm {...thirdsProps} FieldName='Finance_FTL_630-564_Borrower_Dob' />
          </div>
          <div className='form_inputs'>
            <InputerForm {...this.props} FieldName='Finance_FTL_404_Authorized_Nile' />
          </div>
          <div className='form_inputs'>
            <InputerForm {...this.props} FieldName='Finance_FTL_887_Has_Second_Loan' />
          </div>
          {
            InputValuesHelper.GetInputValue(InputValues, 'Finance_FTL_887_Has_Second_Loan') === Consts.OPTION_YES_VALUE &&
              <div className='form_inputs'>
                <div className='form_title2'>Loan 2</div>
              </div>
          }

          <>
            <div className='form_inputs'>
              <InputerForm {...halfsProps} FieldName='Finance_FTL_956-565_Name' />
              <InputerForm {...thirdsProps} FieldName='Finance_FTL_956-261_Account_Number' />
              <InputerForm {...thirdsProps} FieldName='Finance_FTL_956-533_Estimated_Payoff' />
            </div>

            <div className='form_inputs'>
              <InputerForm {...halfsProps} FieldName='Finance_FTL_956-881_Street_Address' />
              <InputerForm {...thirdsProps} FieldName='Finance_FTL_956-831_City' />
              <InputerForm {...thirdsProps} FieldName='Finance_FTL_956-091_State' />
              <InputerForm {...thirdsProps} FieldName='Finance_FTL_956-227_Postal_Code' />
              <div>
                <InputerForm {...thirdsProps} FieldName='Finance_FTL_956-941_Phone' />
                <InputerForm {...thirdsProps} FieldName='Finance_FTL_956-679_Fax' />
              </div>
            </div>
            <InputerForm {...this.props} FieldName='Finance_FTL_545_Authorized_Nile' />

            <div className='form_inputs'>
              <InputerForm {...this.props} FieldName='Finance_FTL_208_Has_Third_Loan' />
            </div>

            {InputValuesHelper.GetInputValue(InputValues, 'Finance_FTL_208_Has_Third_Loan') === Consts.OPTION_YES_VALUE &&
              <div className='form_inputs'>
                <div className='form_title2'>Loan 3</div>
              </div>}
            <>
              <div className='form_inputs'>
                <InputerForm {...halfsProps} FieldName='Finance_FTL_996-844_Name' />
                <InputerForm {...thirdsProps} FieldName='Finance_FTL_996-310_Account_Number' />
                <InputerForm {...thirdsProps} FieldName='Finance_FTL_996-946_Estimated_Payoff' />
              </div>
              <div className='form_inputs'>
                <InputerForm {...halfsProps} FieldName='Finance_FTL_996-784_Street_Address' />
                <InputerForm {...thirdsProps} FieldName='Finance_FTL_996-178_City' />

                <InputerForm {...thirdsProps} FieldName='Finance_FTL_996-878_State' />
                <InputerForm {...thirdsProps} FieldName='Finance_FTL_996-761_Postal_Code' />
                <div>
                  <InputerForm {...thirdsProps} FieldName='Finance_FTL_996-433_Phone' />
                  <InputerForm {...thirdsProps} FieldName='Finance_FTL_996-893_Fax' />
                </div>
              </div>
            </>
          </>
        </>
      </>
    );
  }
}
