import React from 'react';

export default function Label (props) {
  const {
    Periphery
  } = props;

  return (
    <>
      {Periphery.Title}
      {Periphery.SubTitle}
      {Periphery.TitleComponent}
      {Periphery.Note}
      {Periphery.SubNote}
      {Periphery.NoteComponent}
    </>
  );
}
