import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class PropertyMaintenance extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Property Maintenance' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFM_031_Ongoing_Maintenance_Services' />
          <InputerForm {...this.props} FieldName='SFM_031_Other_Services_Explanation' />
        </div>
      </>
    );
  }
}
