import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class Utilities extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Utilities' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFU_285_Water_Source' />
          <InputerForm {...this.props} FieldName='SFU_285_008_t_Text_Explanation' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFU_185_Surcharges_Penalities' />
          <InputerForm {...this.props} FieldName='SFU_185_t_Text_Explanation' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFU_665_Water_Dispose' />
          <InputerForm {...this.props} FieldName='SFU_665_t_Text_Explanation' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFU_213_Is_Gas_Suppiled' />
          <InputerForm {...this.props} FieldName='SFU_656_How_Gas_Supplied' />
          <InputerForm {...this.props} FieldName='SFU_820_Is_Gas_Shut_Off_Installed' />
          <InputerForm {...this.props} FieldName='SFU_670_Defects' />
          <InputerForm {...this.props} FieldName='SFU_670_t_Text_Explanation' />
        </div>
      </>
    );
  }
}
