import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import * as Consts from '../../../shared/Consts';

export default class HOADetailsInformation extends React.Component {
  render () {
    if (this.props.InputValues.HAD_587_Has_Deed_Restrictions !== Consts.OPTION_YES_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='HOA Details / Information' />
        <div className='form_inputs'>
          <div className='b_texts'>We are showing the following HOA/COA items from public records.  Please enter the information in the table below for the correct HOA/COA</div>
          {/* <InputerForm {...this.props} FieldName='HAN_HOA_Item_Count' /> */}
          <InputerForm {...this.props} FieldName='HAN_HOA_Items' />
        </div>
      </>
    );
  }
}
