import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class RegulatedBuildingAndSafety extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Regulated Building and Safety Features' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFR_043_Consent_Title' />

          <div className='title_component-container' style={{ color: '#808080' }}>
            <div className='title_component-item thirds'>Water Conserving Plumbing Fixtures</div>
            <div className='title_component-item thirds'>Quick Release Mechanisms on Window Bars</div>
            <div className='title_component-item thirds'>Properly Braced Tank Water Heater</div>
            <div className='title_component-item thirds'>Smoke Detector</div>
            <div className='title_component-item thirds'>Carbon Monoxide Detector</div>
            <div className='title_component-item thirds'>Gas Shut off Valves</div>
            <div className='title_component-item thirds'>Impact Glazing for Sliding Glass Doors</div>
          </div>
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFR_Consent' />

        </div>
      </>
    );
  }
}
