import React from 'react';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class Nuisance extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Nuisance' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='primary_questionnaire_pqn_076_items' />
          <InputerForm {...this.props} FieldName='primary_questionnaire_pqn_076_other_explanation' />
        </div>
      </>
    );
  }
}
