import React from 'react';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class WireInstructions extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Outgoing Wire Instructions' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTW_441_Domestic_International' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} WidthClassName='halfs' FieldName='Finance_FTW_377_Bank_Name' />
        </div>
        <div className='form_inputs'>
          <div className='form_title2'>Bank Address</div>
          <InputerForm {...this.props} WidthClassName='halfs' FieldName='Finance_FTW_523_Street_Address' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_299_City' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_174_State' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_650_Postal_Code' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_175_Country' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_247_Routing_Number' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_655_Swift_Code' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_440_Account_Name' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_985_Account_Number' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTW_146_For_Further_Credit_To' />
          <InputerForm {...this.props} FieldName='Finance_FTW_708_Note' />
        </div>
      </>
    );
  }
}
