import React from 'react';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class Trustee extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    return (
      <>
        <FormSectionTitle Title='TRUST DETERMINATION' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTR_962_Trustee_Successor' />
        </div>

        {
          InputValuesHelper.GetInputValue(InputValues, 'Finance_FTR_962_Trustee_Successor') === 'Original' &&
            <div className='form_inputs'>
              <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='Original Trustee' />
            </div>
        }

        <div className='form_inputs'>
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTG_091_Social_Security_Number' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTG_227_Drivers_License_Number' />
          <InputerForm {...this.props} WidthClassName='thirds' FieldName='Finance_FTG_941_Date_Of_Birth' />

        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTG_679_Lived_In_California_Last_5_Years' />
          <InputerForm {...this.props} FieldName='Finance_FTG_679_T_Address' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTG_208_Marital_Status' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTG_784_Has_Declared_Bankrupt' />
          <InputerForm {...this.props} FieldName='Finance_FTG_784_T_Status' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTG_178_Has_Unsatisfied_Judgements' />
          <InputerForm {...this.props} FieldName='Finance_FTG_178_T_Text_Explanation' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTG_433-893_Certified' />
        </div>

        <InputerForm {...this.props} FieldName='Finance_FTG_761-u_Upload_Trust_Documents' />

        {InputValuesHelper.GetInputValue(InputValues, 'Finance_FTR_962_Trustee_Successor') === 'Successor' &&
          <div className='form_inputs'>
            <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='Successor Trustee' />
          </div>}

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='Finance_FTS_958_Is_Original_Trustee_Deceased' />
        </div>
        <InputerForm {...this.props} FieldName='Finance_FTS_010-u_Upload_Trust_Documents' />
      </>
    );
  }
}
