import React from 'react';

export default class FooterLastStep extends React.Component {
  render () {
    return (
      <>
        <div className='prev'>
          <div className='prev_btn animated_fastest' onClick={() => this.props.SwitchTabHandler(this.props.SelectedTab - 1)}>Previous</div>
        </div>
        <div className='nexts'>
          <div className='save_btn animated_fastest' onClick={async () => this.props.GotoDashboardHandler()}>Exit</div>
        </div>
        <div className='clearer' />
      </>
    );
  }
}
