import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class VehicleParking extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Vehicle & Parking' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFV_300_Has_Parking' />
          <InputerForm {...this.props} FieldName='SFV_056_Spaces' />
          <InputerForm {...this.props} FieldName='SFV_056_Parking_Space_Items' />
        </div>
      </>
    );
  }
}
