import React from 'react';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import InputerForm from '../../Shared/InputerForm';

export default class Earthquake extends React.Component {
  render () {
    if (this.props.InputValues.BYE_955_Active === undefined || this.props.InputValues.BYE_955_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='Earthquake (PRE 1960)' />

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_034_Earthquake_Anchored_or_Bolted' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_319_Earthquake_Cripple_Walls' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_845_Earthquake_Cripple_Walls_Braced' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_995_Earthquake_Piers_Strengthened' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_744_Earthquake_Unreinforced_Masonry' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_775_Earthquake_Foundation' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_087_Earthquake_Walls_Strengthened' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_333_Earthquake_Hillside' />
          <InputerForm {...this.props} FieldName='BYE_378_Earthquake_Tall_Foundation' />
          <InputerForm {...this.props} FieldName='BYE_532_Earthquake_Tall_Posts' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_204_Earthquake_Living_Area' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYE_197_Earthquake_Garage_Door' />
        </div>
      </>
    );
  }
}
