import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class Pool extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Pool' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFP_536_Pool_Features' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFP_977_Pool_Heat_Type' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFP_977_Spa_Heat_Type' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFP_230_Safety_Features' />
        </div>

        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFP_817_Has_Pool_Defects' />
          <InputerForm {...this.props} FieldName='SFP_817_Pool_Defects_Explanation' />
          <InputerForm {...this.props} FieldName='SFP_183_Has_Spa_Defects' />
          <InputerForm {...this.props} FieldName='SFP_183_Spa_Defects_Explanation' />
        </div>
      </>
    );
  }
}
