import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class WaterConservation extends React.Component {
  render () {
    if (this.props.InputValues.BYW_200_Active === undefined || this.props.InputValues.BYW_200_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='Water Conservation (PRE 1994)' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='BYW_270_Water_Conservation' />
          <InputerForm {...this.props} FieldName='BYW_270_Water_Conservation_Explanation' />
        </div>
      </>
    );
  }
}
